<template>
    <div class="home">
        <ckeditor 
            @ready="onEditorReady" 
            v-model="editorData" 
            @namespaceloaded="onNamespaceLoaded" 
            :config="editorConfig"

            :value="value"
            @input="onInput"

        />
    </div>
</template>
<script>
import Vue from 'vue';
import CKEditor from 'ckeditor4-vue';
Vue.use(CKEditor);
export default {
    props:["value"],/*<------------------------------------------------------------------------------------ */
    components: {
    },
    data() {
        return {
            editorData: this.value,/* <----------------------------------------------------------------------- */
            editorConfig: {
                extraPlugins: 'timestamp,my_modal,image2,justify',
                removePlugins: 'image,exportpdf',
                /*
                toolbarGroups: [
                    { name: 'document', groups: ['mode', 'document', 'doctools'] },
                    { name: 'clipboard', groups: ['clipboard', 'undo'] },
                    { name: 'editing', groups: ['find', 'selection', 'spellchecker', 'editing'] },
                    { name: 'forms', groups: ['forms'] },
                    '/',
                    { name: 'basicstyles', groups: ['basicstyles', 'cleanup'] },
                    { name: 'paragraph', groups: ['list', 'indent', 'blocks', 'align', 'bidi', 'paragraph'] },
                    { name: 'links', groups: ['links'] },
                    { name: 'insert', groups: ['insert'] },
                    '/',
                    { name: 'styles', groups: ['styles'] },
                    { name: 'colors', groups: ['colors'] },
                    { name: 'tools', groups: ['tools'] },
                    { name: 'others', groups: ['others'] },
                    { name: 'about', groups: ['about'] },
                ],
                */
                height: 250,
            }
        };
    },
    mounted(){
            //this.$emit('input',this.editorData);
            //this.value=this.editorData;
            //this.editorData="hello";
    },
    methods: {
        onNamespaceLoaded(CKEDITOR) {
            let that = this;
            CKEDITOR.addCss(".cke_editable{cursor:text; font-size: 11px; font-family: tahoma , Arial, sans-serif;}");
            CKEDITOR.plugins.addExternal('timestamp', 'https://ckeditor.com/docs/ckeditor4/4.21.0/examples/assets/plugins/timestamp/', 'plugin.js');
            CKEDITOR.plugins.addExternal('image2', '/js/ckeditor_4.21.0_full/ckeditor/plugins/image2/', 'plugin.js');
            CKEDITOR.plugins.add('my_modal',
                {
                    init(editor) {
                        editor.addCommand("mySimpleCommand",
                            {
                                exec: function (edt) {
                                    //e.onInsert();
                                    let path = "/images/bao.png";
                                    //D:\NetCore2023_8\NetCore3VueStarterTool_14_3\wwwroot\images\bao.png
                                    edt.insertHtml(`<img src=${path} width="50px" height="40px" />`);
                                }
                            });
                        editor.ui.addButton('SuperButton',
                            {
                                label: "Click me",
                                command: 'mySimpleCommand',
                                toolbar: 'insert',
                                //icon: '/fileupload.png'
                                icon:'/images/bao.png'
                                //D:\NetCore2023_8\NetCore3VueStarterTool_14_3\wwwroot\images\bao.png
                            });
                    }
                });
        },
        onEditorReady(editor) {
        },
        onInput(data){
            this.$emit('input',data);
        }
    }
}
</script>
<style scoped>
.modal.fade {
  z-index: 10000000 !important;
}
</style>
  
  