<template>
    <div class="home">
        <raw-loader :mytxt="require('!raw-loader!./Cke4Vue.vue').default"></raw-loader>
        <h1 class="border-bottom">Home view</h1>
        {{ text }}
        <b-row>
            <b-col lg="3" v-for="(x, k) in ds" :key="k">
                <div class="border p-1 mb-1">
                    <h3 class="border-bottom">{{ x.tieude }}</h3>
                    <div v-html="x.nd"></div>
                    <button @click="onView(x)">View</button>
                    <button @click="onEdit(x)">Edit</button>
                </div>
            </b-col>
        </b-row>
        <form @submit.stop.prevent="onSaveModal">
            <!-- class="modal fade " data-focus="false" id="basicModal"> -->
            <b-modal ref="my-modal1" size="md" :no-close-on-backdrop="true" hide-footer :title="'List Images'">
                <div class="d-block">
                    <div class="">
                        Tieu de : <input v-model="sel.tieude" class="mb-1 full-width" />
                        <CkeditorCom ref="ckeditor" v-model="sel.nd" :height="'200px'"></CkeditorCom>
                    </div>
                    <div class="border-top pt-1">
                        <div class="text-right">
                            <button type="button" @click="onSaveModal" class="btn border mr-1">Save</button>
                            <button type="button" class="btn border" @click="hideModal()">Cancel</button>
                        </div>
                    </div>
                </div>
            </b-modal>
        </form>
    </div>
</template>
<script>
import CkeditorCom from "@/views/Cke4Vue/CkeCom2.vue"
import $ from "jquery";
export default {
    components: {
        CkeditorCom
    },
    data() {
        return {
            ds: [
                {
                    id: 1, tieude: 'Bài viết 1', nd:
                        `

                        <p><img alt="" height="40" src="https://loremflickr.com/320/240" width="53" /></p>
                        <p>Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here Text content goes here</p>
                        


                    `
                },
                { id: 2, tieude: 'Bài viết 2', nd: 'Bài viết 2' },
                { id: 3, tieude: 'Bài viết 3', nd: 'Bài viết 3' },
                { id: 4, tieude: 'Bài viết 4', nd: 'Bài viết 4' },
                { id: 5, tieude: 'Bài viết 5', nd: 'Bài viết 5' },
                { id: 6, tieude: 'Bài viết 6', nd: 'Bài viết 6' },
            ],
            sel: {},
            text:''
        }
    },
    methods: {
        getImgs(html) {
            var getAllImages = html.getElementsByTagName('img');
            for (var i = 0; i < getAllImages.length; i++) {
                (function (x) {  // closure starts here
                    getAllImages[x].addEventListener('click', function () {
                        alert(this.getAttribute('src'))
                    })
                }(i))
            }
        },
        htmlToImgs(html){
            let $div=$(`<div>${html}</div>`);
            let imgs=$div.find('img');
            let kq=[];
            for(var i = 0; i < imgs.length; i++) {
                kq.push(imgs[i].src);
            }
            return kq;
        },
        htmlToText(s) {
            var span = document.createElement('span');
            span.innerHTML = s;
            var children = span.querySelectorAll('*');
            for (var i = 0; i < children.length; i++) {
                if (children[i].textContent)
                    children[i].textContent += ' ';
                else
                    children[i].innerText += ' ';
            }
            return [span.textContent || span.innerText].toString().replace(/ +/g, ' ');
        },
        onView(x) {
            //this.text=this.htmlToText(x.nd); //alert(s);
            alert(this.htmlToImgs(x.nd));
        },
        showModal() {
            this.$refs["my-modal1"].show();
        },
        hideModal() {
            this.$refs["my-modal1"].hide();
        },
        onSaveModal() {
            let r = JSON.parse(JSON.stringify(this.sel));
            let index = this.ds.findIndex(x => x.id == r.id);
            this.ds[index] = r;
            this.sel = {};
            this.hideModal();
        },
        onEdit(x) {
            //alert(JSON.stringify(x));
            this.sel = JSON.parse(JSON.stringify(x));
            this.showModal();
        }
    }

}
</script>
<style scoped>
.full-width {
    width: 100%;
}
</style>
  